.three.columns {
  width: 45% !important;
  padding: 0 !important;
}

.nine.columns {
  width: 55% !important;
  padding-left: 2rem !important;
}

.slideshow-container {
  position: relative;
  width: 700px !important;
  height: 700px !important;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.4);
  margin-left: 2rem !important;
}

.profile-pic {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.8s ease-in-out;
}

/* Zoom levels for different pictures */
.profile-pic-0 {
  transform: scale(1.1);
}

.profile-pic-1 {
  transform: scale(1.8);
  transform-origin: center 40%;
}

.profile-pic-2 {
  transform: scale(1.6);
  transform-origin: center 30%;
}

/* Hover effects */
.slideshow-container:hover .profile-pic {
  transform: scale(1.2);
}

.slideshow-container:hover .profile-pic-1 {
  transform: scale(2);
}

.slideshow-container:hover .profile-pic-2 {
  transform: scale(1.8);
}

/* Navigation dots */
.slideshow-dots {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 12px;
  z-index: 10;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  transition: all 0.3s ease;
}

.dot.active {
  background: white;
  transform: scale(1.2);
}

/* Responsive design */
@media only screen and (max-width: 1400px) {
  .slideshow-container {
    width: 600px !important;
    height: 600px !important;
  }
}

@media only screen and (max-width: 1200px) {
  .slideshow-container {
    width: 500px !important;
    height: 500px !important;
  }
}

@media only screen and (max-width: 992px) {
  .slideshow-container {
    width: 400px !important;
    height: 400px !important;
  }
}

@media only screen and (max-width: 768px) {
  .three.columns,
  .nine.columns {
    width: 100% !important;
    padding: 0 1rem !important;
  }

  .slideshow-container {
    width: 350px !important;
    height: 350px !important;
    margin: 2rem auto !important;
  }
}
